const WebFormList = ({ webFields, removeFromList }) => {
  const list = webFields.map(
    ({ _id, fieldName, fieldInForm, fieldFormVisible, fieldOptions }) =>
      fieldInForm ? (
        <li key={_id}>
          {fieldName}
          {fieldFormVisible
            ? `-> visible in form`
            : fieldOptions.some(e => e.preselected)
                ? `->Hidden!`
                // fieldOptions.filter(e => e.preselected)
                : null}
          <button id={fieldName} onClick={removeFromList}>
            X
          </button>
        </li>
      ) : null
  );

  return <ul>{list}</ul>;
};

export default WebFormList;
